export default function questionsReducers(state = initialState, action) {
  switch (action.type) {
    case "ON_FILER":
      return {
        ...state,
        filter: {
          speciality: action.payload.speciality,
          branche: action.payload.branche,
          category: action.payload.category,
        },
      };
    case "FETCH_QUESTIONS":
      return {
        ...state,
        questions:
          action.payload.page === 0
            ? action.payload.questions
            : state.questions.concat(action.payload.questions),
        page: action.payload.page,
        loading: false,
        hasMore: action.payload.hasMore,
      };
    case "FETCH_QUESTION":
      return {
        ...state,
        question: action.payload.question,
        loading: false,
      };
    case "FOLLOW_QUESTION":
      return {
        ...state,
        questions: (state.questions.find(
          (item) => item.id === action.payload.question_id
        ).is_interested = action.payload.is_interested),
      };
    case "POST_LIKE_QUESTION":
      return {
        ...state,
        ...state,
        question: {
          ...state.question,
          total_likes: Number(state.question.total_likes) + 1,
          is_liked: "1",
        },
        questions: (state.questions || []).map((item, index) => {
          if (item.question_id === action.payload.question_id) {
            item.total_likes = Number(item.total_likes) + 1;
            item.is_liked = "1";
          }
          return item;
        }),
      };
    case "REMOVE_LIKE_QUESTION":
      return {
        ...state,
        question: {
          ...state.question,
          total_likes: Number(state.question.total_likes) - 1,
          is_liked: "0",
        },
        questions: (state.questions || []).map((item, index) => {
          if (item.question_id === action.payload.question_id) {
            item.total_likes = Number(item.total_likes) - 1;
            item.is_liked = "0";
          }
          return item;
        }),
      };
    case "POST_DISLIKE_QUESTION":
      return {
        ...state,
        question: {
          ...state.question,
          total_dislikes: Number(state.question.total_dislikes) + 1,
          is_disliked: "1",
        },
        questions: (state.questions || []).map((item, index) => {
          if (item.question_id === action.payload.question_id) {
            item.total_dislikes = Number(item.total_dislikes) + 1;
            item.is_disliked = "1";
          }
          return item;
        }),
      };
    case "REMOVE_DISLIKE_QUESTION":
      return {
        ...state,
        question: {
          ...state.question,
          total_dislikes: Number(state.question.total_dislikes) - 1,
          is_disliked: "0",
        },
        questions: (state.questions || []).map((item, index) => {
          if (item.question_id === action.payload.question_id) {
            item.total_dislikes = Number(item.total_dislikes) - 1;
            item.is_disliked = "0";
          }
          return item;
        }),
      };
    case "IN_PROCESSING":
      return {
        ...state,
        loading: true,
      };
    case "NEW_QUESION_ADDED":
      return {
        ...state,
        loading: false,
        new_question_id: action.payload.question_id,
      };
    case "CLEAN_ADD_QUESTION":
      return {
        ...state,
        loading: false,
        new_question_id: null,
      };
    case "PRE_UNFOLLOW_USER":
      return {
        ...state,
        question: { ...state.question, is_followed: "0" },
        questions: (state.questions || []).map((item, index) => {
          if (item.user_id === action.payload.user_id) {
            item.is_followed = "0";
          }
          return item;
        }),
      };
    case "PRE_FOLLOW_USER":
      return {
        ...state,
        question: { ...state.question, is_followed: "1" },
        questions: (state.questions || []).map((item, index) => {
          if (item.user_id === action.payload.user_id) {
            item.is_followed = "1";
          }
          return item;
        }),
      };
    case "ERROR_UNFOLLOW_USER":
      return {
        ...state,
        question: { ...state.question, is_followed: "1" },
        questions: (state.questions || []).map((item, index) => {
          if (item.user_id === action.payload.user_id) {
            item.is_followed = "1";
          }
          return item;
        }),
      };
    case "ERROR_FOLLOW_USER":
      return {
        ...state,
        question: { ...state.question, is_followed: "0" },
        questions: (state.questions || []).map((item, index) => {
          if (item.user_id === action.payload.user_id) {
            item.is_followed = "0";
          }
          return item;
        }),
      };
    default:
      return state;
  }
}
